body {
  background: #1b212f;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.genres li {
  display: inline-block;
  border: 1px solid;
  padding: 5px;
  margin: 0 5px 5px 0;
  border-radius: 3px;
  opacity: 0.6;
}

.bg {
  height: 500px;
  position: relative;
  left: 0;
  width: 100%;
  z-index: 1;
  margin-bottom: -330px;
}

.poster-img {
  box-shadow: 0px 5px 20px -5px rgba(0, 0, 0, 0.5);
}

a {
  color: #fff;
  margin-bottom: 10px;
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
  color: #fff;
  margin: 0;
}

input[type='text'] {
  background-color: #fff;
  border-color: #dbdbdb;
  color: #363636;
  box-shadow: inset 0 1px 2px rgba(10, 10, 10, 0.1);
  max-width: 100%;
  width: 100%;
  -webkit-appearance: none;
  align-items: center;
  border: 1px solid transparent;
  border-radius: 4px;
  box-shadow: none;
  display: inline-flex;
  font-size: 1rem;
  justify-content: flex-start;
  line-height: 1.5;
  position: relative;
  vertical-align: top;
  -webkit-box-shadow: 0px 2px 30px -4px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0px 2px 30px -4px rgba(0, 0, 0, 0.6);
  box-shadow: 0px 2px 30px -4px rgba(0, 0, 0, 0.6);
  box-sizing: border-box;
  padding: 1.2em 1.2em;
  margin: 40px 0;
}

.wrapper {
  max-width: 960px;
  margin: 0 auto;
  padding: 0 20px 0 20px;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.actor-bio {
  padding: 0 40px;
}

.movie {
  max-width: 960px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    'poster info'
    'overview overview'
    'cast cast';
  grid-row-gap: 30px;
  grid-template-rows: auto auto;
  grid-template-columns: 220px 1fr;
  z-index: 100;
  position: relative;
  top: 100px;
  padding: 0 20px 0 20px;
}

.overview {
  grid-area: overview;
}

.cast {
  grid-area: cast;
  margin-bottom: 30px;
}

.poster {
  grid-area: poster;
}

.movie-details {
  grid-area: info;
}

.movie-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  grid-auto-rows: auto;
  grid-gap: 40px;
  margin-bottom: 20px;
}

.movie-img {
  max-width: 100%;
  height: auto;
}

.cast_list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  grid-auto-rows: 1fr;
  grid-gap: 40px;
}

.loader {
  border: 2px solid #f3f3f3;
  border-top: 2px solid #74b9ff;
  border-radius: 50%;
  width: 54px;
  height: 54px;
  animation: spin 0.8s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -27px 0 0 -27px;
}

.cast_img {
  border-radius: 10px;
  max-width: 100px;
}

.star {
  align-items: center;
  justify-content: left;
  display: grid;
  grid-gap: 15px;
  grid-template-columns: auto auto;
  margin-bottom: 20px;
  color: #fff;
}

h1 {
  font-size: 42px;
  margin-bottom: 10px;
  line-height: 1.2;
  color: #fff;
}

.logo {
  position: absolute;
  z-index: 2;
  margin-top: 30px;
}

h2 {
  font-size: 28px;
  margin-bottom: 15px;
  color: #fff;
}

h3 {
  text-transform: uppercase;
  font-size: 18px;
  margin-bottom: 15px;
  color: #fff;
}

p {
  opacity: 0.6;
  color: #fff;
}

.movie-title {
  text-align: center;
  margin-top: 10px;
}

select {
  -webkit-appearance: none;
  appearance: none;
  border: 1px solid #255484;
  line-height: 1;
  outline: 0;
  color: #255484;
  border-color: #255484;
  padding: 0.65em 2.5em 0.55em 0.75em;
  border-radius: 3px;
  background: linear-gradient(#255484, #255484) no-repeat,
    linear-gradient(-135deg, rgba(255, 255, 255, 0) 50%, white 50%) no-repeat,
    linear-gradient(-225deg, rgba(255, 255, 255, 0) 50%, white 50%) no-repeat,
    linear-gradient(#255484, #255484) no-repeat;
  background-color: white;
  background-size: 1px 100%, 20px 20px, 20px 20px, 20px 60%;
  background-position: right 20px center, right bottom, right bottom,
    right bottom;
  margin-left: 15px;
}

.sort {
  display: grid;
  align-items: center;
  margin-bottom: 30px;
  grid-template-columns: 70px 120px;
}

.sub-title {
  opacity: 1;
  margin-top: 10px;
}

.load {
  text-align: center;
  margin: 30px 0;
}

.back {
  color: #74b9ff;
  cursor: pointer;
  margin-bottom: 15px;
}

.load-button {
  background-color: #74b9ff;
  color: #fff;
  padding: 15px 20px;
  border-radius: 2px;
  display: inline-block;
  cursor: pointer;
}

.actor {
  display: flex;
  flex-direction: row;
  max-width: 960px;
  margin-bottom: 30px;
}

.flex {
  display: flex;
  flex: 1;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.15);
}

.movie-details-date {
  font-weight: bold;
  opacity: 1;
}

.movie-item {
  margin-top: 1em;
  margin-bottom: 40px;
  display: grid;
  grid-template-columns: 100px 2fr;
  grid-template-areas: 'image content';
  position: relative;
  color: white;
  padding: 20px;
  text-align: left;
}

.movie-item:hover {
  background-color: rgba(0, 0, 0, 0.15);
}

.movie-search-img {
  max-width: 100%;
  height: auto;
  grid-area: image;
}

.movie-search-details {
  grid-area: content;
  margin-left: 30px;
}

.movie-search-title {
  text-align: left;
  margin-top: 10px;
}

.results-title {
  text-align: center;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 767px) {
  .movie {
    grid-template-areas:
      'poster poster'
      'info info'
      'overview overview'
      'cast cast';
    text-align: center;
    grid-row-gap: 30px;
    grid-template-columns: 1fr;
    padding: 0 20px 0 20px;
  }

  .movie-item {
    margin-top: 0;
    margin-bottom: 0;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: 'image' 'content';
    position: relative;
    grid-gap: 20px;
    color: white;
    padding: 20px;
    text-align: left;
  }

  .movie-search-details {
    margin-left: 0;
  }

  .actor {
    flex-direction: column;
  }

  .actor-bio {
    order: 2;
    padding: 40px;
  }

  .actor-img {
    order: 1;
  }

  .bg {
    height: 300px;
    position: relative;
    left: 0;
    width: 100%;
    z-index: 1;
    margin-bottom: -330px;
  }

  .poster-img {
    box-shadow: 0px 5px 20px -5px rgba(0, 0, 0, 0.5);
  }

  .star {
    justify-content: center;
  }
}
